.book-section {
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0.50) 100%), url('../assets/book-a-session.jfif'), lightgray 50% / cover no-repeat;

  background-size: cover;
}

.booking__pagination {
  display: flex;
  gap: 20px;
  align-items: center;

  padding-bottom: 18px;

  border-bottom: .1px solid #eee;
}

.booking__title {
  margin-bottom: 10px;
}

.pagination {
  cursor: pointer;
}

.book-session__caption {
  margin-top: -5px;
}

.booking__heading-titles {
  text-align: center;
  margin: 40px 0;
}

.form__container {
  padding: 0 100px;
  /* border-radius: 20px; */
}

.booking__form {
  padding: 40px 80px;

  background: rgba(255, 255, 255, 0.07);
  backdrop-filter: blur(125px);

  border-radius: 10px;
}

.label__title {
  font-size: 20px;

  display: inline-block;
}

.booking__input-field {
  display: block;
  width: 100%;
  padding: 32px;
  border-radius: 24px;
border: 1.5px solid var(--Gray-4, #BDBDBD);
background: rgba(40, 40, 40, 0.30);

  font-size: 20px;
  font-weight: 400;
  color: #828282;

  outline: none;

  margin-top: 16px;
  margin-bottom: 24px;
}

input[type="date"] {
  text-transform: uppercase;
}

.checkbox {
  margin-top: 20px;
  margin-bottom: 24px;

  display: flex;
  justify-content: space-between;
}

.checkbox-wrap {
  gap: 10px;

  padding: 30px 30px 30px 30px;
  border-radius: 24px;
border-bottom: 1px solid var(--Text, #E5E7EB);

  width: 48%;
}

input[type="checkbox"] {
  margin-right: 10px;
  font: inherit;
  color: transparent;
}

.book__btn {
  display: block;
  color: #121212;
  font-size: 14px;
  font-weight: 400;

  padding: 24px 12px;

  width: 100%;
  height: 60px;

  border-radius: 24px;
  background: #E1DFDD;

  text-align: center;

  margin: 0 auto;

  display: flex;
  align-items: center;
  justify-content: center;

  cursor: pointer;

  transition: all .2s ease-in;
}

.book__btn:hover {
  background: rgba(225, 223, 221, 0.5);
}


@media screen and (max-width: 1025px) {

  .booking__form {
    padding: 40px 40px;
  }
  .booking__input-field {
    padding: 20px;
  }

  .checkbox-wrap {
    padding: 30px 20px;
  }

  .book__btn {
    font-size: 18px;
  }
}

@media screen and (max-width: 774px) {
  .checkbox-wrap {
    padding: 15px 5px;
  }

  .book__btn {
    height: 50px;
  }

  .checkbox {
    flex-direction: column;
    align-items: center;
    
  }

  .checkbox-wrap {
    width: 100%;
    text-align: center;
    padding: 20px;
  }

  .form__container {
    padding: 0 50px;
  }
}

@media screen and (max-width: 774px) {
  .form__container {
    padding: 0 20px;
  }
}

@media screen and (max-width: 490px) {
  .form__container {
    padding: 0px;
  }

  .booking__sub-title {
    font-weight: normal;
  }
}

@media screen and (min-width: 375px) {
  .booking__form {
    padding: 40px 20px;
  }
}